/*=============== SCROLL UP ===============*/
.scrollup {
  position: fixed;
  background: var(--container-color);
  right: 1rem;
  bottom: -20%;
  display: inline-flex;
  padding: 0.3rem;
  border-radius: 0.25rem;
  z-index: var(--z-tooltip);
  opacity: 0.8;
  transition: 0.4s;

  &:hover {
    background-color: var(--first-color);
    opacity: 1;
  }

  &__icon {
    font-size: 1.25rem;
    color: var(--title-color);
  }
}

/* Show Scroll Up*/
.show-scroll {
  bottom: 3rem;
}

/*=============== SCROLL BAR ===============*/
::-webkit-scrollbar {
  width: 0.6rem;
  background: #413e3e;

  &-thumb {
    background: #272525;
    border-radius: 0.5rem;
  }
}
