*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  outline: none;
}

@font-face {
  font-family: Creepster;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("Creepster.3e932ae7.woff2") format("woff2");
  unicode-range: U+??, U+131, U+152-153, U+2BB-2BC, U+2C6, U+2DA, U+2DC, U+304, U+308, U+329, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

:root {
  --header-height: 3.5rem;
  --dropdown-height: 0;
  --rotate-arrow: 0;
  --translate-value: 0;
  --list-opacity: 0;
  --transition-time: .4s;
  --transition-timing: cubic-bezier(.25, .46, .45, .94);
  --border-radius: 1.4rem;
  --list-button-height: 1.8rem;
  --floating-icon-size: 26;
  --floating-icon-top: 0;
  --floating-icon-left: 0;
  --primary-bg-color: var(--color-dark);
  --primary-color: var(--color-light);
  --button-hover-bg-color: var(--color-dark);
  --border-color: var(--color-light);
  --body-color: linear-gradient(90deg, var(--color-light) 0%, var(--color-dark) 100%);
  --container-color: linear-gradient(136deg, lighten(var(--color-light)) 0%, lighten(var(--color-dark)) 100%);
  --container-color-repeating: repeating-linear-gradient(135deg, var(--color-light), var(--color-light) 10px, var(--color-dark) 10px, var(--color-dark) 20px  );
  --hue: 14;
  --first-color: hsl(var(--hue), 91%, 54%);
  --first-color-alt: hsl(var(--hue), 91%, 50%);
  --title-color: hsl(var(--hue), 4%, 100%);
  --text-color: hsl(var(--hue), 4%, 85%);
  --text-color-light: hsl(var(--hue), 4%, 55%);
  --text-active: var(--text-color);
  --body-font: Creepster, Helvetica Neue, Helvetica, Segoe UI, sans-serif;
  --biggest-font-size: 2.25rem;
  --h1-font-size: 1.75rem;
  --h2-font-size: 1.3333rem;
  --h3-font-size: 1.15rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;
  --font-medium: 400;
  --font-semi-bold: 400;
  --font-black: 400;
  --mb-0-25: .25rem;
  --mb-0-5: .5rem;
  --mb-0-75: .75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --z-tooltip: 10;
  --z-fixed: 100;
}

:root.forest {
  --color-dark: #3e6b4f;
  --color-light: #8fbf9f;
}

:root.fire {
  --color-dark: #ff4500;
  --color-light: #ff8c00;
}

:root.earth {
  --color-dark: #ffc154;
  --color-light: #f5d76e;
}

:root.air {
  --color-dark: #00bcd4;
  --color-light: #e0f2f1;
}

:root.water {
  --color-dark: #09c;
  --color-light: #6cf;
}

:root.high {
  --color-dark: #222;
  --color-light: #fff;
}

@media screen and (min-width: 992px) {
  :root {
    --biggest-font-size: 3rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.75rem;
    --h3-font-size: 1.3333rem;
    --normal-font-size: 1.15rem;
    --small-font-size: .938rem;
    --smaller-font-size: .813rem;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: var(--header-height) 0 0 0;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  background: var(--body-color);
  color: var(--text-color);
}

h1, h2, h3, h4 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

button, input {
  border: none;
  outline: none;
}

button {
  cursor: pointer;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

.section {
  padding: 4.5rem 0 2rem;
}

.section__title {
  font-size: var(--h2-font-size);
  margin-bottom: var(--mb-2);
  text-align: center;
}

.container {
  margin-left: var(--mb-1-5);
  margin-right: var(--mb-1-5);
  max-width: 968px;
}

.grid {
  display: grid;
}

.main {
  overflow: hidden;
}

.header {
  background-color: var(--body-color);
  z-index: var(--z-fixed);
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.nav {
  height: var(--header-height);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav__logo {
  font-weight: var(--font-medium);
  align-items: center;
  column-gap: .5rem;
  display: flex;
}

.nav__logo-img {
  width: 1.25rem;
}

.nav__link, .nav__logo, .nav__toggle, .nav__close {
  color: var(--title-color);
}

.nav__toggle {
  cursor: pointer;
  font-size: 1.25rem;
}

@media screen and (max-width: 767px) {
  .nav__menu {
    background: var(--container-color);
    z-index: var(--z-fixed);
    border-radius: 0 0 1.5rem 1.5rem;
    width: 100%;
    padding: 3.5rem 0;
    transition: all .4s;
    position: fixed;
    top: -150%;
    left: 0;
  }
}

.nav__img {
  width: 100px;
  position: absolute;
  top: 0;
  left: 0;
}

.nav__close {
  cursor: pointer;
  font-size: 1.8rem;
  position: absolute;
  top: .5rem;
  right: .7rem;
}

.nav__list {
  flex-direction: column;
  align-items: center;
  row-gap: 1.5rem;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  font-weight: var(--font-black);
  transition: all .4s;
}

.nav__link:hover {
  color: var(--text-color);
}

.nav__theme {
  max-width: 11rem;
}

.nav__theme .color-switcher {
  width: 1.5rem;
  height: 1.5rem;
  padding: 0;
}

.nav__theme .color-switcher.forest-switcher {
  background-color: #3e6b4f;
}

.nav__theme .color-switcher.fire-switcher {
  background-color: #ff4500;
}

.nav__theme .color-switcher.earth-switcher {
  background-color: #ffc154;
}

.nav__theme .color-switcher.air-switcher {
  background-color: #00bcd4;
}

.nav__theme .color-switcher.water-switcher {
  background-color: #09c;
}

.nav__theme .color-switcher.high-switcher {
  background-color: #222;
}

.show-menu {
  background: var(--container-color-repeating);
  top: 0;
}

.scroll-header {
  background: var(--container-color-repeating);
}

.active-link {
  position: relative;
}

.active-link:before {
  content: "";
  background-color: var(--title-color);
  border-radius: 50%;
  width: 5px;
  height: 5px;
  position: absolute;
  bottom: -.75rem;
  left: 45%;
}

.home__content {
  row-gap: 1rem;
}

.home__group {
  padding-top: 2rem;
  display: grid;
  position: relative;
}

.home__img {
  justify-self: center;
  height: 250px;
}

.home__indicator {
  background-color: var(--title-color);
  border-radius: 50%;
  width: 8px;
  height: 8px;
  position: absolute;
  top: 7rem;
  right: 2rem;
}

.home__indicator:after {
  content: "";
  background-color: var(--title-color);
  width: 1px;
  height: 48px;
  position: absolute;
  top: -3rem;
  right: 45%;
}

.home__details-img {
  position: absolute;
  right: .5rem;
}

.home__details-title, .home__details-subtitle {
  font-size: var(--small-font-size);
  text-align: right;
  display: block;
}

.home__subtitle {
  font-size: var(--h3-font-size);
  text-transform: uppercase;
  margin-bottom: var(--mb-1);
}

.home__title {
  font-size: var(--biggest-font-size);
  font-weight: var(--font-black);
  margin-bottom: var(--mb-1);
  line-height: 109%;
}

.home__description {
  margin-bottom: var(--mb-1);
}

.home__buttons {
  justify-content: space-between;
  display: flex;
}

.swiper-pagination {
  position: initial;
  margin-top: 1rem;
}

.swiper-pagination-bullet {
  background-color: var(--title-color);
  opacity: 1;
  width: 5px;
  height: 5px;
}

.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 .5rem;
}

.swiper-pagination-bullet-active {
  border-radius: .5rem;
  width: 1.5rem;
  height: 5px;
}

.button {
  background-color: var(--first-color);
  color: var(--title-color);
  font-weight: var(--font-medium);
  border-radius: .5rem;
  padding: 1rem 1.75rem;
  transition: all .3s;
  display: inline-block;
}

.button:hover {
  background-color: var(--first-color-alt);
}

.button--ghost {
  background-color: #0000;
  border: 2px solid;
  border-radius: 3rem;
  padding: .75rem 1.5rem;
}

.button--ghost:hover {
  background: none;
}

.button--link {
  color: var(--title-color);
}

.button--flex {
  align-items: center;
  column-gap: .5rem;
  display: inline-flex;
}

.category__container {
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem 2rem;
}

.category__data {
  text-align: center;
}

.category__data:hover .category__img {
  transform: translateY(-.5rem);
}

.category__img {
  margin-bottom: var(--mb-0-75);
  width: 120px;
  transition: all .3s;
}

.category__title {
  margin-bottom: var(--mb-0-25);
}

.about__container {
  row-gap: 2rem;
}

.about__data {
  text-align: center;
}

.about__description {
  margin-bottom: var(--mb-2);
}

.about__img {
  justify-self: center;
  width: 200px;
  animation: 2s ease-in-out infinite floating;
}

.trick__container {
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
  padding-top: 1rem;
}

.trick__content {
  background: var(--container-color);
  text-align: center;
  border-radius: 1rem;
  padding: 1.5rem 0 1rem;
  position: relative;
  overflow: hidden;
}

.trick__content:hover .trick__img {
  transform: translateY(-.5rem);
}

.trick__content:hover .trick__button {
  right: 0;
}

.trick__img {
  width: 90px;
  transition: all .3s;
}

.trick__subtitle, .trick__price {
  display: block;
}

.trick__subtitle {
  font-size: var(--smaller-font-size);
  margin-bottom: var(--mb-0-5);
}

.trick__title, .trick__price {
  color: var(--title-color);
  font-weight: var(--font-medium);
  font-size: var(--normal-font-size);
}

.trick__button {
  border-radius: .25rem .25rem .75rem;
  padding: .5rem;
  display: inline-flex;
  position: absolute;
  bottom: 0;
  right: -3rem;
}

.trick__icon {
  color: var(--title-color);
  font-size: 1.25rem;
}

.discount__container {
  background: var(--container-color);
  border-radius: 1rem;
  row-gap: .75rem;
  padding: 2.5rem 0 1.5rem;
}

.discount__data {
  text-align: center;
}

.discount__title {
  font-size: var(--h2-font-size);
  margin-bottom: var(--mb-2);
}

.discount__img {
  justify-self: center;
  width: 200px;
}

.new__container {
  padding-top: 1rem;
}

.new__img {
  margin-bottom: var(--mb-0-5);
  width: 120px;
  transition: all .3s;
}

.new__content {
  background: var(--container-color);
  text-align: center;
  border-radius: .75rem;
  width: 242px;
  padding: 2rem 0 1.5rem;
  position: relative;
  overflow: hidden;
}

.new__content:hover .new__img {
  transform: translateY(-.5rem);
}

.new__content:hover .new__button {
  right: 0;
}

.new__tag {
  background-color: var(--first-color);
  color: var(--title-color);
  font-size: var(--small-font-size);
  border-radius: .25rem;
  padding: .25rem .5rem;
  position: absolute;
  top: 8%;
  left: 8%;
}

.new__title {
  font-size: var(--normal-font-size);
  font-weight: var(--font-medium);
}

.new__subtitle {
  font-size: var(--small-font-size);
  margin-bottom: var(--mb-0-75);
  display: block;
}

.new__prices {
  align-items: center;
  column-gap: .5rem;
  display: inline-flex;
}

.new__price {
  font-weight: var(--font-medium);
  color: var(--title-color);
}

.new__discount {
  color: var(--first-color);
  font-size: var(--smaller-font-size);
  font-weight: var(--font-medium);
  text-decoration: line-through;
}

.new__button {
  border-radius: .25rem .25rem .75rem;
  padding: .5rem;
  display: inline-flex;
  position: absolute;
  bottom: 0;
  right: -3rem;
}

.new__icon {
  font-size: 1.25rem;
}

.newsletter__description {
  text-align: center;
  margin-bottom: var(--mb-1-5);
}

.newsletter__form {
  background: var(--container-color);
  border-radius: .75rem;
  justify-content: space-between;
  padding: 1rem;
  display: flex;
}

.newsletter__input {
  color: var(--title-color);
  background: none;
  width: 70%;
  padding: 0 .5rem;
}

.newsletter__input::placeholder {
  color: var(--text-color);
}

.footer {
  position: relative;
  overflow: hidden;
}

.footer__img-one, .footer__img-two {
  transition: all .3s;
  position: absolute;
}

.footer__img-one {
  width: 100px;
  top: 6rem;
  right: -2rem;
}

.footer__img-one:hover {
  transform: translateY(-.5rem);
}

.footer__img-two {
  width: 150px;
  bottom: 4rem;
  right: 4rem;
}

.footer__img-two:hover {
  transform: translateY(-.5rem);
}

.footer__container {
  align-items: end;
  row-gap: 2rem;
}

.footer__content {
  text-align: center;
}

.footer__logo {
  margin-bottom: var(--mb-1);
  font-weight: var(--font-medium);
  color: var(--title-color);
  align-items: center;
  column-gap: .5rem;
  display: flex;
}

.footer__logo-img {
  width: 20px;
}

.footer__description {
  margin-bottom: var(--mb-1);
}

.footer__social {
  column-gap: .75rem;
  display: flex;
}

.footer__social-dropdown svg {
  width: 1.6rem;
  height: 1.6rem;
}

.footer__social-dropdown .text-truncate {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.footer__social-dropdown .dropdown-container {
  flex-direction: column;
  width: 100%;
  max-width: 34rem;
  margin-top: 30vh;
  display: flex;
}

.footer__social-dropdown .dropdown-title-icon, .footer__social-dropdown .dropdown-arrow {
  display: inline-flex;
}

.footer__social-dropdown .dropdown-title {
  text-transform: capitalize;
  margin: 0 auto 0 .8rem;
}

.footer__social-dropdown .dropdown-button {
  align-items: center;
  padding: 0 .8rem;
  display: flex;
}

.footer__social-dropdown .dropdown-button:hover, .footer__social-dropdown .dropdown-button:focus {
  color: var(--text-active);
}

.footer__social-dropdown .dropdown-button:hover svg, .footer__social-dropdown .dropdown-button:focus svg {
  fill: var(--text-active);
}

.footer__social-dropdown .dropdown-button svg {
  fill: var(--text-color);
  transition: all var(--transition-time) var(--transition-timing);
  margin-right: .8rem;
}

.footer__social-dropdown .dropdown-button span {
  margin: 0 auto;
}

.footer__social-dropdown .dropdown-button span i {
  margin-right: .8rem;
}

.footer__social-dropdown .dropdown-button svg, .footer__social-dropdown .dropdown-button span {
  pointer-events: none;
}

.footer__social-dropdown .main-button {
  border-radius: var(--border-radius);
  color: var(--text-color);
  background-color: var(--primary-bg-color);
  border: .1rem solid var(--border-color);
  transition: all var(--transition-time) var(--transition-timing);
  height: 2.8rem;
}

.footer__social-dropdown .main-button:focus {
  border: .1rem solid var(--primary-color);
  box-shadow: 0 0 0 .2rem #2c62f666;
}

.footer__social-dropdown .main-button .dropdown-arrow {
  transition: transform var(--transition-time) var(--transition-timing);
  transform: rotate(var(--rotate-arrow));
  margin-left: .8rem;
}

.footer__social-dropdown .list-button {
  height: var(--list-button-height);
  transition: color var(--transition-time) var(--transition-timing);
  color: var(--text-color);
  cursor: none;
  overflow: hidden;
}

.footer__social-dropdown .dropdown-list-container {
  max-height: var(--dropdown-height);
  transition: max-height var(--transition-time) var(--transition-timing);
  overflow: hidden;
}

.footer__social-dropdown .dropdown-list-wrapper {
  background-color: var(--primary-bg-color);
  border-radius: var(--border-radius);
  border: .1rem solid var(--border-color);
  margin-top: 1rem;
  padding: 1rem;
  position: relative;
}

.footer__social-dropdown ul.dropdown-list {
  list-style-type: none;
  position: relative;
}

.footer__social-dropdown ul.dropdown-list:before {
  content: "";
  z-index: 0;
  opacity: 0;
  height: var(--list-button-height);
  background-color: var(--button-hover-bg-color);
  transition: all var(--transition-time) linear;
  transform: translateY(var(--translate-value));
  border-radius: var(--border-radius);
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.footer__social-dropdown ul.dropdown-list:hover:before, .footer__social-dropdown ul.dropdown-list:hover ~ .floating-icon {
  opacity: 1;
}

.footer__social-dropdown li.dropdown-list-item {
  z-index: 1;
  opacity: var(--list-opacity);
  transition: opacity .8s var(--transition-timing);
  flex-direction: column;
  display: flex;
  position: relative;
}

.footer__social-dropdown .floating-icon {
  height: calc(var(--floating-icon-size) * 1px);
  width: calc(var(--floating-icon-size) * 1px);
  top: var(--floating-icon-top);
  left: var(--floating-icon-left);
  background-color: var(--border-color);
  pointer-events: none;
  opacity: 0;
  transition: opacity var(--transition-time) var(--transition-timing);
  z-index: 2;
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
  display: inline-flex;
  position: absolute;
}

.footer__social-dropdown .floating-icon svg {
  fill: var(--text-active);
}

.footer__title {
  font-size: var(--h3-font-size);
  margin-bottom: var(--mb-1);
}

.footer__links {
  row-gap: .35rem;
  display: grid;
}

.footer__link {
  font-size: var(--small-font-size);
  color: var(--text-color);
  transition: all .3s;
}

.footer__link:hover {
  color: var(--title-color);
}

.footer__copy {
  text-align: center;
  font-size: var(--smaller-font-size);
  margin-top: 4.5rem;
  display: block;
}

.scrollup {
  background: var(--container-color);
  z-index: var(--z-tooltip);
  opacity: .8;
  border-radius: .25rem;
  padding: .3rem;
  transition: all .4s;
  display: inline-flex;
  position: fixed;
  bottom: -20%;
  right: 1rem;
}

.scrollup:hover {
  background-color: var(--first-color);
  opacity: 1;
}

.scrollup__icon {
  color: var(--title-color);
  font-size: 1.25rem;
}

.show-scroll {
  bottom: 3rem;
}

::-webkit-scrollbar {
  background: #413e3e;
  width: .6rem;
}

::-webkit-scrollbar-thumb {
  background: #272525;
  border-radius: .5rem;
}

@media screen and (max-width: 320px) {
  .container {
    margin-left: var(--mb-1);
    margin-right: var(--mb-1);
  }

  .home__img {
    height: 200px;
  }

  .home__buttons {
    flex-direction: column;
    row-gap: 1rem;
    width: max-content;
  }

  .category__container, .trick__container {
    grid-template-columns: .8fr;
    justify-content: center;
  }
}

@media screen and (min-width: 576px) {
  .about__container {
    grid-template-columns: .8fr;
    justify-content: center;
  }

  .newsletter__container {
    grid-template-columns: .7fr;
    justify-content: center;
    display: grid;
  }

  .newsletter__description {
    padding: 0 3rem;
  }
}

@media screen and (min-width: 767px) {
  body {
    margin: 0;
  }

  .section {
    padding: 7rem 0 2rem;
  }

  .nav {
    height: calc(var(--header-height)  + 1.5rem);
  }

  .nav__img, .nav__close, .nav__toggle {
    display: none;
  }

  .nav__list {
    flex-direction: row;
    column-gap: 3rem;
  }

  .nav__link {
    text-transform: initial;
    font-weight: initial;
  }

  .home__content {
    grid-template-columns: repeat(2, 1fr);
